import React, { useContext,useRef,useEffect } from 'react';
import { observer } from 'mobx-react';
import ReactToPrint from "react-to-print";
import PropTypes from 'prop-types';
import history from '../../utilitis/history';
import StoreContext from '../../stores';
import Input from '../commons/Input';
// import Button from '../commons/Button';
import Loading from '../commons/Loading';
import Print from '../prints';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import logo from '../../assetes/images/logo.png';

const styles = {
    root: {
        flexGrow: 1,
        height: '100vh',
    },
    inputLabel: {
        right: 0,
        left: 'auto',
        fontFamily: 'Vazir'

    },
    logo: {
        width: '4em',
        margin: 0,
    },
    title:{
        flex:1,
        color:'#fff',
        margin:0,
        paddingRight: '1.5em',
    },
    select:{
        border: '1px solid #7e64ab',
        padding: '1em',
        margin: '.4em',
        width: '21.5em',
        outline: 'none',
        borderRadius: '7px',
        background: 'rgba(248, 247, 250, 0.53)',
        fontFamily: 'Vazir',
        '&:focus': {
            background: '#fff'
        }
    },
    PrintBtn: {
        background: '#fff',
        color: ' #7E64AB',
        fontFamily: 'Vazir',
        width: '90%',
        border: 'none',
        borderRadius: '30px',
        padding: '.8em 1em',
        fontSize: '20px',
        fontWeight: '600',
        boxShadow: '1px 12px 13px rgba(126, 100, 171, 0.5490196078431373)',
        outline: 'none',
        margin: '1em auto'
    },
    error: {
        color: '#ff1100'
    }

};
const Order = props => {

    const { classes } = props;
    const { FarmerModel, ProductModel, OrderModel, BuyerModel, LoginStore, CompanyModel} = useContext(StoreContext);
    const lgPrintRef = useRef();
    const mdPrintRef = useRef();
    const smPrintRef = useRef();
    const title = CompanyModel.companyName;
    const tel = CompanyModel.companyTel;
    const printType=CompanyModel.printType;
    const user = LoginStore.writer;
    const factorNumber = "0";
    const date = OrderModel.date;
    const product = OrderModel.product;
    const price = OrderModel.price;
    const count = OrderModel.number;
    const weight = OrderModel.weight;
    const farmer = OrderModel.farmer;
    const buyer = OrderModel.buyer;
    const address = CompanyModel.companyAddress;
    const total = OrderModel.total;
    const totalSumation = OrderModel.totalSumation;
    const isLoading = OrderModel.loading;
    // const isLogin = LoginStore.isLogin;
    // if (isLogin == false) {
    //     history.push('/');
    // }
    let buttonPrint;
    if (printType==0){
        buttonPrint = <ReactToPrint
            trigger={() => <button className={classes.PrintBtn} type="submit">{isLoading ? <Loading /> : 'چاپی گەورە'}</button>}
            content={() => lgPrintRef.current}
        />;
    } else if (printType==1){
        buttonPrint = <ReactToPrint
            trigger={() => <button className={classes.PrintBtn} type="submit">{isLoading ? <Loading /> : 'چاپی ناوەندی'}</button>}
            content={() => mdPrintRef.current}
        />;
    } else if (printType == 2){
        buttonPrint = <ReactToPrint
            trigger={() => <button className={classes.PrintBtn} type="submit">{isLoading ? <Loading /> : 'چاپی گچکە'}</button>}
            content={() => smPrintRef.current}
        />; 
    } else{
        buttonPrint=<span>error</span>;
    }
    useEffect(() => {
        FarmerModel.loadFarmers();
        BuyerModel.loadBuyers();
    }, []);
    const handelChangeFarmer=e=>{
        e.preventDefault();
        const element=e.target[e.target.selectedIndex];
        const id = element.getAttribute('name');
        ProductModel.updateCuurentFarmer(id);
        OrderModel.updateFarmer(element.value,id);

        ProductModel.loadProducts();
    };
    const handelChangeProduct=e=>{
        e.preventDefault();
        const element = e.target[e.target.selectedIndex];
        const id = element.getAttribute('name');
        OrderModel.updateProduct(element.value, id);
    };
    const handelChanegPrice = e => {
        e.preventDefault();
        OrderModel.updatePrice(e.target.value);
    };
    const handelChanegWeight = e => {
        e.preventDefault();
        OrderModel.updateWeight(e.target.value);

    };
    const handelChanegNumber = e => {
        e.preventDefault();
        OrderModel.updateNumber(e.target.value);
    };
    const handelChangeBuyer=e=>{
        e.preventDefault();
        const element = e.target[e.target.selectedIndex];
        const id = element.getAttribute('name');
        OrderModel.updateBuyer(element.value, id);
    };
    const handelSubmit = e => {
        e.preventDefault();
        if (farmer == '' && product == '' && price == '' && weight == '' && count == '' && buyer == '') {
            OrderModel.ChangeError('تواوی فیلدکان دبیت پر کیوه');
        } else{
            OrderModel.sale();
            OrderModel.emptyForm();
            FarmerModel.loadFarmers();
            BuyerModel.loadBuyers();
            CompanyModel.companyInformation();
        }
    };
    return (
        <div>
            <div className={classes.root}>
                <Grid container>
                    <Grid item xs={12} sm={12}>
                        <Box p={2} pb={1} display="flex" justifyContent="center" alignItems="center">
                            <img className={classes.logo} src={logo} />
                            <h1 className={classes.title}>فرۆش</h1>
                        </Box>
                        <form onSubmit={handelSubmit}>
                            <Box display="flex" flexDirection="column"
                                justifyContent="center"
                                alignItems="center"
                            >
                                <select onChange={handelChangeFarmer} className={classes.select}>
                                    <option hidden>که‌س</option>
                                    {
                                        FarmerModel.farmers.map(item => {
                                            return (<option name={item.id} value={item.title} key={item.id}>{item.title}</option>);
                                        })
                                    }
                                </select>
                                <select onChange={handelChangeProduct} className={classes.select}>
                                    <option hidden>کاڵا یا بەرهەم</option>
                                    {
                                        ProductModel.products.map(item => {
                                            return (<option name={item.id} value={item.titleProduct} key={item.id}>{item.titleProduct}</option>);
                                        })
                                    }
                                </select>
                                <Input type="number" placeholder="نرخ" value={OrderModel.price} onChange={handelChanegPrice} />
                                <Input type="number" placeholder="کێش" value={OrderModel.weight} onChange={handelChanegWeight} />
                                <Input type="number" placeholder="دانه" value={OrderModel.number} onChange={handelChanegNumber} />
                                <select onChange={handelChangeBuyer} className={classes.select}>
                                    <option hidden>کڕیار</option>
                                    {
                                        BuyerModel.buyers.map(item => {
                                            return (<option name={item.id} value={item.title} key={item.id}>{item.title}</option>);
                                        })
                                    }
                                </select>
                                <span className={classes.error}>{OrderModel.error}</span>
                            </Box>
                            <Box display="flex"
                                justifyContent="center"
                                alignItems="center"
                                mt={2}
                            >   
                                {buttonPrint}
                                
                                
                            </Box>
                            
                            <div className="printDisplay"> 
                                <Print
                                    size="lg"
                                    title={title}
                                    tel={tel}
                                    user={user}
                                    factorNumber={factorNumber}
                                    date={date}
                                    product={product}
                                    price={price}
                                    count={count}
                                    weight={weight}
                                    farmer={farmer}
                                    buyer={buyer}
                                    address={address}
                                    total={total}
                                    totalSumation={totalSumation}
                                    ref={lgPrintRef} />
                                <Print 
                                    size="md"
                                    title={title}
                                    tel={tel}
                                    user={user}
                                    factorNumber={factorNumber}
                                    date={date}
                                    product={product}
                                    price={price}
                                    count={count}
                                    weight={weight}
                                    farmer={farmer}
                                    buyer={buyer}
                                    address={address}
                                    total={total}
                                    totalSumation={totalSumation}
                                    ref={mdPrintRef} />
                            
                                <Print 
                                    size="sm"
                                    title={title}
                                    tel={tel}
                                    user={user}
                                    factorNumber={factorNumber}
                                    date={date}
                                    product={product}
                                    price={price}
                                    count={count}
                                    weight={weight}
                                    farmer={farmer}
                                    buyer={buyer}
                                    address={address}
                                    total={total}
                                    totalSumation={totalSumation}
                                    ref={smPrintRef} />
                            </div>

                        </form>
                    </Grid>
                </Grid>
            </div>
        </div>
    );
};
Order.propTypes = {
    classes: PropTypes.object.isRequired,
};
export default withStyles(styles)(observer(Order));
