/* eslint-disable no-param-reassign */

import { types, flow } from 'mobx-state-tree';

const ProductContent = types.model("product", {
    id: types.number,
    codePeople: types.number,
    titlePeople: types.optional(types.string, ''),
    codeProduct: types.number,
    titleProduct: types.optional(types.string, ''),
    peopleId: types.number,
    productId: types.number,
    date: types.optional(types.string, ''),
    price: types.number,
    isActive: types.boolean,
    isDelete: types.boolean,
    cost: types.number
});

const productModel = types.model('productModel', {
    products: types.optional(types.array(ProductContent), []),
    cuurentFarmer: types.optional(types.string, ''),

})

    .actions(self => ({
        
        updateProducts(json) {
            let products = [];
            json.map(item => {
                products = [ ...products,item ];
            });
            self.products = [ ...products ];
        },

        loadProducts: flow(function* loadFarmers() {
            const res = yield window.fetch(`https://shop.narmamer.com/api/buy/${self.cuurentFarmer}`, {
                method: 'GET',
            });
            const json = yield res.json();
            self.updateProducts(json);
        }),
        updateCuurentFarmer(id){
            self.cuurentFarmer = id;
        }

    }));

const ProductModel = productModel.create();

export default ProductModel;
