import { types, flow } from 'mobx-state-tree';
const BuyerContent = types.model("buyer", {
    id: types.number,
    code: types.number,
    title: types.optional(types.string, ''),
    tel: types.optional(types.string, ''),
    address: types.optional(types.string, ''),
    description: types.optional(types.string, ''),
    type: types.number,
    isActive: types.boolean,
    isSale: types.boolean,
    isBuy: types.boolean

});

const buyerModel = types.model('buyerModel', {
    buyers: types.optional(types.array(BuyerContent), []),
})

    .actions(self => ({
        updateBuyers(json) {
            let buyers=[];
            json.map(item => {
                buyers = [ ...buyers, item ];
            });
            self.buyers = [ ...buyers ];
        },

        loadBuyers: flow(function* loadBuyers() {
            const res = yield window.fetch('https://shop.narmamer.com/api/people/2', {
                method: 'GET',
            });
            const json = yield res.json();
            self.updateBuyers(json);
        })

    }));

const BuyerModel = buyerModel.create();


export default BuyerModel;
