
import { types, flow } from 'mobx-state-tree';

const companyModel = types.model('companyModel', {
    companyName: types.optional(types.string, ''),
    companyAddress: types.optional(types.string, ''),
    companyTel: types.optional(types.string, ''),
    printType: types.optional(types.number,0),
})
    .actions(self => ({

        companyInformation: flow(function* companyInformation() {
            const res = yield window.fetch(`https://shop.narmamer.com/api/company/1`, {
                method: 'GET',
            });
            const json = yield res.json();
            const company = json[0];
            self.updateCompany(company.title, company.address, company.tel, company.printType);
        }),
        updateCompany(name,address,tel,printType){
            self.companyName = name;
            self.companyAddress= address;
            self.companyTel=tel;
            self.printType = printType;
        }

    }));

const CompanyModel = companyModel.create();


export default CompanyModel;
