/* eslint-disable no-param-reassign */

import { types,flow  } from 'mobx-state-tree';
import { values } from "mobx";
const FarmerContent = types.model("farmer", {
    id: types.number,
    code:types.number,
    title: types.optional(types.string,''),
    tel:  types.optional(types.string,''),
    address: types.optional(types.string,''),
    description:  types.optional(types.string,''),
    type: types.number,
    isActive: types.boolean,
    isSale: types.boolean,
    isBuy: types.boolean

});

const farmerModel = types.model('farmerModel', {
    farmers: types.optional(types.array(FarmerContent),[]),
})
    .views(self => ({
        getfarmers() {
            return values(self.farmers);
        }
    }))
    .actions(self => ({
        updateFarmers(json) {
            let farmers=[];
            json.map(item => {
                farmers = [ ...farmers, item ];
            });
            self.farmers = [ ...farmers ];

        },

        loadFarmers: flow(function* loadFarmers(){
            const res = yield window.fetch('https://shop.narmamer.com/api/people/1', {
                method: 'GET',
            });
            const json = yield res.json();
            self.updateFarmers(json);
        })

    }));

const FarmerModel = farmerModel.create();
export default FarmerModel;
