import { types ,flow,getSnapshot} from 'mobx-state-tree';

const orderModel = types.model('orderModel', {
    farmer:types.optional(types.string,''),
    farmerId:types.optional(types.number,0),
    product: types.optional(types.string, ''),
    productId:types.optional(types.number,0),
    price: types.maybeNull(types.string),
    weight: types.maybeNull(types.string),
    number: types.maybeNull(types.string),
    buyer: types.optional(types.string, ''),
    buyerId: types.optional(types.number, 0),
    loading: types.optional(types.boolean, false),
    error: types.optional(types.string, ''),

})
    .views(self => ({
        get total() {
            if (self.price && self.weight != null){
                return parseFloat(self.price) * parseFloat(self.weight);
            }else{
                return 0;
            }
        },
        get totalSumation(){
            if (self.price && self.weight != null) {
                return self.total / 100 * 15 + self.total;
            } else {
                return 0;
            }
            
        },
        get date(){
            const dateNow = new Date();
            const day = ("0" + dateNow.getDate()).slice(-2);
            const month = ("0" + (dateNow.getMonth() + 1)).slice(-2);
            const year = dateNow.getFullYear();
            let date = `${year}-${month}-${day}`;
            return date;
        }
    }))
    .actions(self => ({

        emptyForm() {
            self.price = '';
            self.weight='';
            self.number='';
            console.log(getSnapshot(OrderModel));
        },
        sale: flow(function* sale() {
            const data = {
                PeopleID:self.farmerId,
                BuyID: self.productId,
                Serial:0,
                Date: self.date,
                Price:parseFloat(self.price),
                Weight: parseFloat(self.weight),
                Count: parseFloat(self.number),
                SumPrice: self.total,
                isResive:true,
                IsDelete:false,
                isStop:false,
                Description:'',
                Resive:0 ,
                Discount:0,
                Result:0,
            };
            const res = yield window.fetch(`https://shop.narmamer.com/api/sales`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(data),
            });
            const json = yield res.json();
            console.log(json);
            console.log(JSON.stringify(data));
        }),
        updateFarmer(farmer,id){
            self.farmer=farmer;
            self.farmerId = Number(id);
        },
        updateProduct(product, id) {
            self.product = product;
            self.productId = Number(id);
        },
        updatePrice(price){
            self.price =price;
        },
        updateWeight(weight) {
            self.weight = weight;
        },
        updateNumber(number) {
            self.number = number;
        },
        updateBuyer(buyer,id){
            self.buyer=buyer;
            self.buyerId = Number(id);
        },
        changeLoading(value = false) {
            self.loading = value;
        },
        ChangeError(error) {
            self.error = error;
            self.changeLoading(false);
        }, 
    }));

const OrderModel = orderModel.create();


export default OrderModel;
