import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

const styles = {
    formBtn: {
        background: '#fff',
        color: ' #7E64AB',
        fontFamily: 'Vazir',
        width: '90%',
        border: 'none',
        borderRadius: '30px',
        padding: '.8em 1em',
        fontSize: '20px',
        fontWeight: '600',
        boxShadow: '1px 12px 13px rgba(126, 100, 171, 0.5490196078431373)',
        outline:'none',
        margin:'0 auto'

    }
};

const Button = ({ type, text,classes }) => {
    return (
        <button className={classes.formBtn} type={type}>
            {text}
        </button>
    );
};
Button.defaultProps = {
    type: 'button',
};
Button.propTypes = {
    classes: PropTypes.object.isRequired,
    type: PropTypes.string,
    text: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.element,
    ]).isRequired,
};

export default withStyles(styles)(Button);
