import React, { useContext, useEffect } from 'react';
import { observer } from 'mobx-react';

import Welcome from '../elements/welcome';
import Input from '../commons/Input';
import Button from '../commons/Button';
import Loading from '../commons/Loading';

import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import logo from '../../assetes/images/logo.png';
import StoreContext from '../../stores';

const styles = {
    root: {
        flexGrow: 1,
        height: '100vh',
    },
    inputLabel: {
        right: 0,
        left: 'auto',
        fontFamily: 'Vazir'

    },
    logo: {
        width: '10em',
        marginTop: '1em',
    },
    error:{
        color:'#ff1100'
    },
    form:{
        display: 'flex',
        flexDirection: 'column',
        alignContent: 'space-between',
        justifyContent: 'space-around',
        height:'70%',
    },


};
const Login = props => {
    const { classes } = props;
    const { LoginStore, CompanyModel} = useContext(StoreContext);
    const isLoading = LoginStore.loading;

    useEffect(() => {
        CompanyModel.companyInformation();
    }, []);
    const handelChangeUsername = e => {
        LoginStore.UpdateUser(e.target.value);
    };

    const handelChangePassword = e => {
        LoginStore.UpdatePassword(e.target.value);
    };
    const handelSubmit = e => {
        e.preventDefault();
        const userName = LoginStore.userName;
        const password = LoginStore.password;
        if (userName && password !== null || userName && password !== ''){
            LoginStore.loginRequest(userName, password);
        } else{
            LoginStore.ChangeError('نێوی بەکارهێنەر یا وشەی نهێنی بنوسە');
        }
    };
    return (
        <div>
            <div className={classes.root}>
                <Grid container>
                    <Grid item xs={12} sm={12}>
                        <Box display="flex" justifyContent="center">
                            <img className={classes.logo} src={logo} />
                        </Box>
                        <Box p={0} pr={4}>
                            <Welcome title="بەخێرهاتن" subTitle={CompanyModel.companyName} />
                        </Box>

                        <form className={classes.form} onSubmit={handelSubmit}>
                            <Box display="flex" flexDirection="column"
                                justifyContent="center"
                                alignItems="center"
                            >
                                <Input type="text" placeholder="نێوی بەکارهێنەر" value={LoginStore.userName} onChange={handelChangeUsername} />
                                <Input type="password" placeholder="وشەی نەهێنی" value={LoginStore.password} onChange={handelChangePassword} />
                                <span className={classes.error}>{LoginStore.error}</span>
                            </Box>
                            <Button type="submit" text={isLoading ? <Loading /> : 'چوونە ژوور'} />
                        </form>
                        <button id="btnAdd">install</button>
                    </Grid>
                </Grid>
            </div>

        </div>
    );
};
Login.propTypes = {
    classes: PropTypes.object.isRequired,
};
export default withStyles(styles)(observer(Login));
