
import { types} from 'mobx-state-tree';
import history from '../utilitis/history';
import request from '../utilitis/request';

const loginStore = types.model('loginStore', {
    loading: types.optional(types.boolean, false),
    isLogin: types.optional(types.boolean, false),
    userName:types.optional(types.string,''),
    password: types.optional(types.string, ''),
    writer: types.optional(types.string, ''),
    error: types.optional(types.string, ''),
})
    .actions(self => ({
        loginRequest(userName,password) {
            self.userName = userName;
            self.password = password;
            this.changeLoading(true);
        
            return new Promise((resolve, reject) => {
                request.post('/Login',
                    {
                        UserName:userName,
                        Password:password
                    }).then(( {data} ) => {

                    if(data!==false){
                        self.updateWriter(data.title);
                        history.push('/order');
                        self.changeLogin(true);
                    } else{
                        self.ChangeError('نێوی بەکارهێنەر یا وشەی نهێنی هەڵەیە');
                        self.changeLogin(false);
                    }
                })
                    .catch(error => {
                        reject(error);
                        self.ChangeError('کێشەی پەیوەندی دەگەڵ سێرڤێر');
                        this.changeLoading(false);
                    });

                setTimeout(() => {
                    this.changeLoading(false);
                    resolve();
                }, 3000);
            });
        },
        UpdateUser(value) {
            self.userName = value;
            self.ChangeError('');
        },
        UpdatePassword(value) {
            self.password = value;
            self.ChangeError('');
        },
        updateWriter(name){
            self.writer =name;
        },

        changeLoading(value = false) {
            self.loading = value;
        },
        ChangeError(error) {
            self.error = error;
            self.changeLoading(false);
        },
        changeLogin(status){
            self.isLogin=status;
        }
    }));
const LoginStore = loginStore.create();


export default LoginStore;
